$gold: #dcaa30;


html,
body {
    height: 100%;
    padding-top: 25px;
    font-size: 1rem;
    line-height: 1.3rem;
    font-family: 'hp_simplifiedlight';
    font-weight: 200;
}

a {
	color: #0096d6;
	&:hover {
		color: #0171ad;
		text-decoration: none;
	}
}


.btn {
	text-transform: uppercase;
	font-weight: 300;
	background: #ffffff;
	border-color: #0171ad;
	color: #0096d6;
	border-radius: 3px;
	&:hover {
		background: #0096d6;
		border-color: #0096d6;
		color: #ffffff;
	}
}

.wrapper,
.row {
    height: 100%;
    margin-left: 0;
    margin-right: 0;
}

.wrapper:before,
.wrapper:after,
.content-column:before,
.content-column:after {
    content: "";
    display: table;
}

.wrapper:after,
.content-column:after {
    clear: both;
}

#sidebar {
    background-color: #5a5a5a;
    height: 100%;
    position: fixed;
    right: 0;
}

.content-column {
	padding: 0;
}

.navbar {
	padding: 15px 15px 10px;
	.navbar-brand {
		padding: 0;
		}
	}

